import React from 'react';
import './App.css';


import {AuthProvider, useAuth} from "./context/auth";
import {AuthWidget} from "./view/authenticated";
import {UnAuthWidget} from "./view/unauthenticated";

function App() {
    const {user} = useAuth()
    return (
        <div className="App">
            {user ? <AuthWidget/> : <UnAuthWidget/>}
        </div>
    );
}


export default App;
