import React from "react";
import styled from "styled-components";
import {LogInWidget} from "@/widget/base/log_in_wap";
import {useForm} from "react-hook-form";
import {InputWidget} from "@/widget/form/input";
import {SpanWidget} from "@/widget/base/span";

import loginmimaImg from "@/assets/images/loginmima.png";
import loginicon01Img from "@/assets/images/loginicon01.png";
import loginming from "@/assets/images/loginming.png";
import loginyanzhengma from "@/assets/images/loginyanzhengma.png";
import bigbtnImg from "@/assets/images/bigbtn.png"
import {useHistory, useParams} from "react-router-dom";
import {TisSuccess} from "@/view/log_in/tis_success";


export const ForgotPad: React.FC = () => {

    const {isLog} = useParams()
    const {register, errors, handleSubmit, setValue} = useForm();
    const {push, goBack} = useHistory();
    let title = isLog ? "修改密码" : "找回密码"
    return (<LogInWidget title={title}>
        <ForgotPadStyle>
            <InputWidget errors={errors}
                         inputRef={register({
                             required: 'Please enter a number > 0',
                         })}
                         img={loginming}
                         label={'amount'}
                         placeholder={'请输入手机号码'}
                         setValue={(ev) => {
                             setValue(ev, "")
                         }}
            />
            <SpanWidget size={10}/>

            <InputWidget errors={errors}
                         inputRef={register({
                             required: 'Please enter a number > 0',
                         })}
                         img={loginicon01Img}
                         label={'amount'}
                         placeholder={'验证码'}
                         setValue={(ev) => {
                             setValue(ev, "")
                         }}
            />
            <SpanWidget size={10}/>

            <InputWidget errors={errors}
                         inputRef={register({
                             required: 'Please enter a number > 0',
                         })}
                         img={loginyanzhengma}
                         label={'amount'}
                         placeholder={'请输入新密码'}
                         setValue={(ev) => {
                             setValue(ev, "")
                         }}
            />
            <SpanWidget size={10}/>

            <InputWidget errors={errors}
                         inputRef={register({
                             required: 'Please enter a number > 0',
                         })}
                         img={loginmimaImg}
                         label={'amount'}
                         placeholder={'请再次输入新密码'}
                         setValue={(ev) => {
                             setValue(ev, "")
                         }}
            />

            <div className="buttons">
                <div className={'btn'} onClick={() => {
                    if (isLog) {
                        goBack();
                        push(`/me/tis_success/${TisSuccess.MidfPad}`);
                    }
                }}>完成
                </div>
            </div>
        </ForgotPadStyle>
    </LogInWidget>);
}


const ForgotPadStyle = styled.div`

  padding: 40px 30px;
  box-sizing: border-box;
  width: 94%;
  margin: auto;
  background: rgba(255, 255, 255, 1);
  opacity: 1;
  border-radius: 15px;


  .buttons {
    margin-top: 50px;

    > div {
      background: url("${bigbtnImg}") 100% 100%;
      background-size: 100% 100%;
      width: 290px;
      height: 49px;
      margin: 0 auto 0 auto;
      display: flex;
      justify-content: center;
      align-content: start;
      font-size: 16px;
      font-weight: 500;
      line-height: 49px;
      color: #FFFFFF;

      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
`